import React from 'react';
import { arrayOf, string, shape } from 'prop-types';

import styles from './Features.module.scss';

const Features = ({ params }) => (
  <section className={styles.features__container}>
    <div className={styles.items__content}>
      <h2
        className='extra-small'
        dangerouslySetInnerHTML={{ __html: params && params.title }}
      />

      <div className={styles.items__container}>

        {params && params.items && params.items.map(({ color, icon, description }, index) => (
          <div className={styles.feature__container}
              key={index}>
            <i className='material-icons' style={{ color }}>
              {icon}
            </i>

            <p className="copytext">
              {description}
            </p>
          </div>
        ))}
      </div>
    </div>
  </section>
);

Features.propTypes = {
  params: shape({
    title: string,
    items: arrayOf(shape({
      title: string,
      icon: string,
      color: string,
      description: string
    }))
  })
};

Features.defaultProps = {
  params: {
    items: []
  }
};

export default Features;



