import React from 'react';
import Select from 'react-select';
import 'react-select/dist/react-select.css';

import './styles.scss';

export class ColorSelectorControl extends React.Component{
  selectColor = (e) => {
    this.props.onChange(e.value);
  }

  renderOption(option) {
    if (!option) {
      return null;
    }

    const value = typeof option === 'string' ? option : option.value;

    return (
      <div className="colorSelector__tester">
        <div style={{ backgroundColor: value }}>
        </div>
        {value}
      </div>
    );
  }

  render() {
    const selectedColor = colors.find(item => item.value === this.props.value);

    return (
      <div className='colorSelector__container'>
        <div className='colorSelector__colored'
          style={{ backgroundColor: selectedColor && selectedColor.value }} />
        <Select
          name='colorSelector'
          searchable={false}
          options={colors}
          optionRenderer={this.renderOption}
          valueRenderer={this.renderOption}
          onChange={this.selectColor} />
      </div>
    );
  }
};

export class ColorSelectorPreview extends React.Component {
  render() {
    return (
      <div>
        Color selector preview...
      </div>
    )
  }
};

